body{
    background-image: url(/archit-1997/To-Do-List-App.git/static/media/notes.bf2c6155.jpg);
    opacity: .85;
  }
  .App{
    /*background-color:rgba(64, 209, 202, 0.836);*/
    background-image: url(/archit-1997/To-Do-List-App.git/static/media/input-back.d690c864.jpg);
    min-height:600px;
    width: 500px;
    margin-top: 90px;
    margin-left: 500px;
    opacity: 1.5;
    border-radius: 20px;
    border-style: double;
    border-width: 5px;
    border-color: black;
  }

  h1{
    margin-left: 170px;
    padding-top: 20px;
    color: black;
  }
  
  #to-do-form input{
    background-color: rgb(88, 189, 88);
    border-style: groove;
    border-color: black;
    color:black;
    width: 200px;
    height: 50px;
    margin: 50px;
    padding-left: 40px;
    font-size: 18px;
    border-radius: 10px;
  }
  #to-do-form input::-webkit-input-placeholder{
    color: black;
  }
  #to-do-form input:-ms-input-placeholder{
    color: black;
  }
  #to-do-form input::-ms-input-placeholder{
    color: black;
  }
  #to-do-form input::placeholder{
    color: black;
  }
  #to-do-form input:focus{
    outline: none;
  }
  #to-do-form button{
    height: 50px;
    width: 80px;
    border-radius: 5px;
    border-style: groove;
    border-color: black;
    background-color: rgb(41, 174, 207); 
    font-size: 15px;
    cursor: pointer;
  }
  #to-do-form button:focus{
    outline: none;
  }
.list{
    color: #fff;
    width: 300px;
    margin: 20px auto;
    height: 50px;
    padding-bottom: 7px;
    background-color: rgb(76, 172, 76);
    border-radius: 5px;
    border-style: groove;
    border-color: black;
}
.list p{
    padding: 5px;
    position: relative;
    transition: ease-in-out;
}
.list p input{
    background-color: transparent;
    border: 0;
    color:black;
    font-size: 18px;
}
.list p input:focus{
    outline: none;
}
.list p span{
    position: absolute;
    right: 10px;
}
.faicons{
    margin-top: 10px;
    font-size: 15px;
    cursor: pointer;
    color: rgb(248, 77, 77);
}
.itemsAnim-enter{
    opacity: 0.01;
}
.itemsAnim-enter .itemsAnim-enter-active{
    opacity: 1;
    transition: opacity 300ms ease-in
}
.itemsAnim-leave{
    opacity: 1;
}
.itemsAnim-leave .itemsAnim-leave-active{
    opacity: 0.01;
    transition: opacity 300ms ease-in
}
